.loader {
  background-color: rgba(0, 0, 0, 0.438);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
}

.loader-relative {
  position: absolute;
  width: 100%;
  height: 100%;
}

.loader-absolute {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 100vh !important;
}

.relativePosition {
  position: relative;
}

.fixedPosition {
  position: fixed;
}
